        let gameCode = this.$route.params.id;
        this.setCookie('currentGameName', encodeURI(this.$route.meta.title), 0.5) //safari   

        gamestatus(this, gameCode, function(res) {
            if (Object.keys(res).length === 0) {
                alert("");
                t.$router.push({ path: "/index" });
                return;
            } //vuex 
            t.$store.dispatch("updatebetGameCode", gameCode);
            _getbettime(t, gameCode, t.time); //
            _payRate(t, gameCode, "1", t.payrate); //

        });
    },
    methods: {
        //arrCreated
        arrCreated: function(arr) {
            let arrayReturn = [];
            arr.forEach(function(item) {
