            LS: [
                { id: "AddZodiac-2", name: "", odds: 0, checked: false, imgURL: require("@/assets/img/icon_mouse.svg"), play: 0 },
                { id: "AddZodiac-2", name: "", odds: 0, checked: false, imgURL: require("@/assets/img/icon_cattle.svg"), play: 1 },
                { id: "AddZodiac-2", name: "", odds: 0, checked: false, imgURL: require("@/assets/img/icon_tiger.svg"), play: 2 },
                { id: "AddZodiac-2", name: "", odds: 0, checked: false, imgURL: require("@/assets/img/icon_rabbit.svg"), play: 3 },
