                { id: "AddZodiac-2", name: "", odds: 0, checked: false, imgURL: require("@/assets/img/icon_snake.svg"), play: 5 },
                { id: "AddZodiac-2", name: "", odds: 0, checked: false, imgURL: require("@/assets/img/icon_horse.svg"), play: 6 },
                { id: "AddZodiac-2", name: "", odds: 0, checked: false, imgURL: require("@/assets/img/icon_sheep.svg"), play: 7 },
                { id: "AddZodiac-2", name: "", odds: 0, checked: false, imgURL: require("@/assets/img/icon_monkey.svg"), play: 8 },
