                { id: "AddZodiac-2", name: "", odds: 0, checked: false, imgURL: require("@/assets/img/icon_dog.svg"), play: 10 },
                { id: "AddZodiac-2", name: "", odds: 0, checked: false, imgURL: require("@/assets/img/icon_pig.svg"), play: 11 }
            ],
