            LW: [
                { id: 'AddDigitsInOnes-2', name: '0', odds: 0, checked: false, play: 0 },
                { id: 'AddDigitsInOnes-2', name: '1', odds: 0, checked: false, play: 1 },
                { id: 'AddDigitsInOnes-2', name: '2', odds: 0, checked: false, play: 2 },
                { id: 'AddDigitsInOnes-2', name: '3', odds: 0, checked: false, play: 3 },
                { id: 'AddDigitsInOnes-2', name: '4', odds: 0, checked: false, play: 4 },
                { id: 'AddDigitsInOnes-2', name: '5', odds: 0, checked: false, play: 5 },
                { id: 'AddDigitsInOnes-2', name: '6', odds: 0, checked: false, play: 6 },
                { id: 'AddDigitsInOnes-2', name: '7', odds: 0, checked: false, play: 7 },
                { id: 'AddDigitsInOnes-2', name: '8', odds: 0, checked: false, play: 8 },
                { id: 'AddDigitsInOnes-2', name: '9', odds: 0, checked: false, play: 9 },
            ],
            //
            ZXBZTitle: ['', '', '', '', '', '', '', '', ],
            ZXBZArr,
            offTitle: 0,
            OffSubTitle: 0,
            option_slide_idx: null,
            payrate: { value: {} },
            time: { value: '' },
            barMax: 0, //--->
            rebatRate: 0, //
            forbet: [], //   
            quantity: 0,
            currentName: '', //
            issueTrace:false, //
            mask:true
        };
    },
    mounted() {
        window.scrollTo(0,0);
        let t = this         
        let gameCode = this.$route.params.id;
        this.setCookie('currentGameName',encodeURI(this.$route.meta.title),0.5) //safari    
         gamestatus(this, gameCode, function(res) {             
            if (Object.keys(res).length === 0) {
                alert('')
                t.$router.push({ path: '/index' });
                return;
            }
            //vuex 
            t.$store.dispatch("updatebetGameCode", gameCode)   
            _getbettime(t, gameCode, t.time); //
            _payRate(t, gameCode, '1', t.payrate); //            
        }) 
    },
    methods: {
        //    : FN
        clickOff: function(clickOffValue, clickSubValue) {
            this.offTitle = clickOffValue;
            this.OffSubTitle = clickSubValue;
            this.currentName = this.gameMenu[clickOffValue].sub[clickSubValue].subName
        },

        // 
        childByValue: function() {
            let GameType = [
                this.TM,
                this.LM,
                this.SixX1,
                this.SixMixArr,
                this.SB,
                this.BB,
                this.BBB,
                this.QSB,
                this.Zodiac,
                this.ZX,
                this.TWArr,
                this.ZTWSArr,
                this.QMWHArr,
                this.LMaArr,
                this.LS,
                this.LW,
                this.ZXBZArr,
            ];
            for (let j = 0; j < GameType.length; j++) {
                for (let i = 0; i < GameType[j].length; i++) {
                    GameType[j][i].checked = false;
                }
            }
            this.option_slide_idx = null
            this.quantity = 0
            this.forbet = [] //            
            this.rebatRate=0
        },
        bundledatainit() { //
            let _t = this
            let GameType = [
                this.TM,
                this.LM,
                this.SixX1,
                this.SixMixArr,
                this.SB,
                this.BB,
                this.BBB,
                this.QSB,
                this.Zodiac,
                this.ZX,
                this.TWArr,
                this.ZTWSArr,
                this.QMWHArr,
                this.LMaArr,
                this.LS,
                this.LW,
                this.ZXBZArr,
            ];
            if (Object.keys(this.payrate.value).length > 0) {
                GameType.forEach(function(v, i) {
                    _t.renderdata(v)
                })
            }
            this.mask=false
        },
        // ;(+61);
        getcolor(number) {
            var red = [1, 2, 7, 8, 12, 13, 18, 19, 23, 24, 29, 30, 34, 35, 40, 45, 46];
            var blue = [3, 4, 9, 10, 14, 15, 20, 25, 26, 31, 36, 37, 41, 42, 47, 48];
            var green = [5, 6, 11, 16, 17, 21, 22, 27, 28, 32, 33, 38, 39, 43, 44, 49];

            var css = "redback";
            if (red.indexOf(parseInt(number)) > -1) css = "redback";
            if (blue.indexOf(parseInt(number)) > -1) css = "blueback";
            if (green.indexOf(parseInt(number)) > -1) css = "greenback";

            return css;
        }, //
        bobackground(str) {
            if (str.indexOf('Red') > 0)
                return 'big-redback'
            else if (str.indexOf('Blue') > 0)
                return 'big-blueback'
            else if (str.indexOf('Green') > 0)
                return 'big-greenback'
            else if (str.indexOf('Tie') > 0)
                return 'big-noneback'
        },
        renderdata(obj) { //
            let _t = this
            obj.forEach(function(v, i) {
                let _p = _t.payrate.value[v.id];
                v.odds = calPayRate(_p, 0);
                v.odds2 = calPayRate2(_p, 0)
                v.playTypeS = _p.playTypeS;
                v.playTypeM = _p.playTypeM;
                _t.barMax = _p.rebatRate
            })

        },
        barrenderdata(obj) { //bar 
            let _t = this
            obj.forEach(function(v, i) {
                let _p = _t.payrate.value[v.id];
                v.odds = calPayRate(_p, _t.rebatRate);
                v.odds2 = calPayRate2(_p, _t.rebatRate)
            })
        },
        getQuantity(e, posArr) {
            switch (e.playTypeS) {
                case 545:
                case 546:
                case 547:
                case 548:
                case 552:
                    { //                            
                        return combination(posArr.length, 2);
                    }
                case 543:
                case 544:
                case 549:
                case 553:
                    { //   
                        return combination(posArr.length, 3);
                    }
                case 542:
                case 550:
                case 554:
                    { //  
                        return combination(posArr.length, 4);
                    }
                case 551:
                case 556:
                case 555:
                    { // 
                        return combination(posArr.length, 5);
                    }
                case 557:
                    { //
                        return combination(posArr.length, 6);
                    }
                case 558:
                    { //
                        return combination(posArr.length, 7);
                    }
                case 559:
                    { //
                        return combination(posArr.length, 8);
                    }
                case 560:
                    { //
                        return combination(posArr.length, 9);
                    }
                case 561:
                    { //
                        return combination(posArr.length, 10);
                    }
                case 562:
                    { //
                        return combination(posArr.length, 11);
                    }
                case 563:
                    { //
                        return combination(posArr.length, 12);
                    }
                default:
                    return 1;

            }
        },
        //
        checkNum(Arr) {
            let _t = this
            this.forbet = []
            let checkedArr = Arr.filter(function(v, i) {
                return v.checked == true
            })
            
            if(this.option_slide_idx==5 && checkedArr.length>0) this.option_slide_idx =null  //
            let posArr = [],posCnArr=[]
            checkedArr.forEach(function(e, i) {
                switch (_t.offTitle) {                    
                    case 15:
                    case 16:
                    case 17:
                    case 18:
                        posArr.push(e.play)
                        posCnArr.push(e.name)
                        let q = _t.getQuantity(e, posArr)
                        let p = ["", "", "", "", "", "", "", ""]
                        p[0] = posArr.toString()

                        let tplay=p[0];
                        if(_t.offTitle==16){
                            //position
                            //
                            tplay = posCnArr.toString()
                        }

                        if ((checkedArr.length - 1) == i && q > 0) {
                            _t.forbet.push({
                                uid: randomRangeId(10),
                                id: e.id,
                                play: tplay,
                                name: _t.currentName,
                                betMainInfo: {
                                    playTypeM: e.playTypeM,
                                    playTypeS: e.playTypeS,
                                    positions: p,
                                    quantity: q,
                                    payRate: e.odds,
                                    payRate2: e.odds2,
                                    rebatRate: _t.rebatRate,
                                    unitPrice: 0
                                }
                            });
                        }
                        break;
                    default:
                        _t.forbet.push({
                            uid: randomRangeId(10),
                            id: e.id,
                            play: e.play,
                            name: _t.currentName,
                            betMainInfo: {
                                playTypeM: e.playTypeM,
                                playTypeS: e.playTypeS,
                                positions: ["", "", "", "", "", "", "", ""],
                                quantity: _t.getQuantity(e),
                                payRate: e.odds,
                                payRate2: e.odds2,
                                rebatRate: _t.rebatRate,
                                unitPrice: 0
                            }
                        })
                        break;
                }
            })

            let Plus = 0
            this.forbet.forEach(function(v, i) {
                Plus += v.betMainInfo.quantity
            });

            this.quantity = Plus;
        },
    },
    watch: {
        payrate: {
            deep: true,
            handler(nv, ov) {
                this.bundledatainit()                              
            }
        },
        offTitle: function() {
            let _this = this;
            switch (this.offTitle) {
                case 3:
                    {
                        //1-6
                        this.SixMixArr.forEach(function(v, i) {
                            v.id = "SixMix-F" + (_this.OffSubTitle + 1) + "-" + subEn[i];
                        });
                        this.renderdata(this.SixMixArr)

                    }
                    break;
                case 8: //
                    {
                        this.Zodiac.forEach(function(v, i) {
                            v.id = "Zodiac-" + zodiacEn[i];
                        });
                        this.renderdata(this.Zodiac)

                    }
                    break;
                case 9: //
                    {
                        this.Zodiac.forEach(function(v, i) {
                            v.id = "ZodiacSix-" + zodiacEn[i];
                        });
                        this.renderdata(this.Zodiac)
                    }
                    break;
                case 10: //
                    {
                        this.Zodiac.forEach(function(v, i) {
                            v.id = "ZodiacAll-" + zodiacEn[i];
                        });
                        this.renderdata(this.Zodiac)
                    }
                    break;
                case 15: //-
                    {
                        this.oddsmask = 1
                        this.LMaArr.forEach(function(v, i) {
                            v.id = "AddNum-2";
                        });
                        this.renderdata(this.LMaArr)

                    }
                    break;
                case 16: //-
                    {
                        this.LS.forEach(function(v, i) {
                            v.id = "AddZodiac-2";
                        });
                        this.renderdata(this.LS)

                    }
                    break;
                case 17: //-
                    {
                        this.LW.forEach(function(v, i) {
                            v.id = "AddDigitsInOnes-2";
                        });
                        this.renderdata(this.LW)

                    }
                    break;
                case 18: //-
                    {
                        this.ZXBZArr.forEach(function(v, i) {
                            v.id = "AllOut-5";
                        });
                        this.renderdata(this.ZXBZArr)

                    }
                    break;
            }
        },

        OffSubTitle: function() {
            let _this = this;
            switch (this.offTitle) {
                case 3:
                    {
                        //1-6
                        this.SixMixArr.forEach(function(v, i) {
                            v.id = "SixMix-F" + (_this.OffSubTitle + 1) + "-" + subEn[i];
                        });
                        this.renderdata(this.SixMixArr)

                    }
                    break;
                case 15:
                    {
                        //
                        if (this.OffSubTitle == 0) {
                            this.LMaArr.forEach(function(v, i) {
                                v.id = "AddNum-2";
                            });
                        }
                        if (this.OffSubTitle == 1) {
                            this.LMaArr.forEach(function(v, i) {
                                v.id = "AddNum-3";
                            });
                        }
                        if (this.OffSubTitle == 2) {
                            this.LMaArr.forEach(function(v, i) {
                                v.id = "AddNum-4";
                            });
                        }
                        if (this.OffSubTitle == 3) {
                            this.LMaArr.forEach(function(v, i) {
                                v.id = "AddNum-3in2";
                            });
                        }
                        if (this.OffSubTitle == 4) {
                            this.LMaArr.forEach(function(v, i) {
                                v.id = "AddNum-2S";
                            });
                        }
                        if (this.OffSubTitle == 5) {
                            this.LMaArr.forEach(function(v, i) {
                                v.id = "AddNum-S";
                            });
                        }
                        this.renderdata(this.LMaArr)
                    }
                    break;
                case 16:
                    {
                        //
                        if (this.OffSubTitle == 0) {
                            this.LS.forEach(function(v, i) {
                                v.id = "AddZodiac-2";
                            });
                        }
                        if (this.OffSubTitle == 1) {
                            this.LS.forEach(function(v, i) {
                                v.id = "AddZodiac-3";
                            });
                        }
                        if (this.OffSubTitle == 2) {
                            this.LS.forEach(function(v, i) {
                                v.id = "AddZodiac-4";
                            });
                        }
                        if (this.OffSubTitle == 3) {
                            this.LS.forEach(function(v, i) {
                                v.id = "AddZodiac-5";
                            });
                        }
                        this.renderdata(this.LS)                        
                    }
                    break;
                case 17:
                    {
                        //
                        if (this.OffSubTitle == 0) {
                            this.LW.forEach(function(v, i) {
                                v.id = "AddDigitsInOnes-2";
                            });
                        }
                        if (this.OffSubTitle == 1) {
                            this.LW.forEach(function(v, i) {
                                v.id = "AddDigitsInOnes-3";
                            });
                        }
                        if (this.OffSubTitle == 2) {
                            this.LW.forEach(function(v, i) {
                                v.id = "AddDigitsInOnes-4";
                            });
                        }
                        if (this.OffSubTitle == 3) {
                            this.LW.forEach(function(v, i) {
                                v.id = "AddZodiac-5";
                            });
                        }
                        this.renderdata(this.LW)                        
                    }
                    break;
                case 18:
                    {
                        //
                        if (this.OffSubTitle == 0) {
                            this.ZXBZArr.forEach(function(v, i) {
                                v.id = "AllOut-5";
                            });
                        }
                        if (this.OffSubTitle == 1) {
                            this.ZXBZArr.forEach(function(v, i) {
                                v.id = "AllOut-6";
                            });
                        }
                        if (this.OffSubTitle == 2) {
                            this.ZXBZArr.forEach(function(v, i) {
                                v.id = "AllOut-7";
                            });
                        }
                        if (this.OffSubTitle == 3) {
                            this.ZXBZArr.forEach(function(v, i) {
                                v.id = "AllOut-8";
                            });
                        }
                        if (this.OffSubTitle == 4) {
                            this.ZXBZArr.forEach(function(v, i) {
                                v.id = "AllOut-9";
                            });
                        }
                        if (this.OffSubTitle == 5) {
                            this.ZXBZArr.forEach(function(v, i) {
                                v.id = "AllOut-10";
                            });
                        }
                        if (this.OffSubTitle == 6) {
                            this.ZXBZArr.forEach(function(v, i) {
                                v.id = "AllOut-11";
                            });
                        }
                        if (this.OffSubTitle == 7) {
                            this.ZXBZArr.forEach(function(v, i) {
                                v.id = "AllOut-12";
                            });
                        }
                        this.renderdata(this.ZXBZArr)                        
                    }
                    break;
            }
        },
        rebatRate(nv, ov) {
            let _t = this
            this.rebatRate = nv

            let GameType = [
                this.TM,
                this.LM,
                this.SixX1,
                this.SixMixArr,
                this.SB,
                this.BB,
                this.BBB,
                this.QSB,
                this.Zodiac,
                this.Zodiac,
                this.Zodiac,
                this.ZX,
                this.TWArr,
                this.ZTWSArr,
                this.QMWHArr,
                this.LMaArr,
                this.LS,
                this.LW,
                this.ZXBZArr,
            ];
            //
            this.barrenderdata(GameType[this.offTitle])

            // 
            if (this.forbet.length > 0) {
                this.forbet.forEach(function(v, i) {
                    let _p = _t.payrate.value[v.id];
                    v.betMainInfo.payRate = calPayRate(_p, nv);
                    v.betMainInfo.payRate2 = calPayRate2(_p, nv)
                    v.betMainInfo.rebatRate = nv
                })
            }
        }
    },
    destroyed() {
        const unWatch = this.$watch('payrate')
        unWatch(); // watch       
        this.childByValue() 
        cleartimer()
    },
    computed: {
        donotTouch(){
            return this.$store.state.dontTouch
        }
    }

};
</script>

